import React from 'react'
import MenuItemText from '../atom/menuItemText'
import { MenuItem } from '@material-tailwind/react'

const navMenuItems = ({ menuOpen, navItems }: { menuOpen: boolean, navItems: { name: string, url: string }[] }) => {
  return (
    <ul className={`lg:flex lg:items-center lg:gap-6 ${menuOpen ? "block" : "hidden"} w-full lg:w-auto mt-2 lg:mt-0`}>
      {navItems.map((item, index) => (
        <MenuItemText menuItemText={item.name} isActive={false} itemUrl={item.url} index={index} key={index} />
      ))}
    </ul>
  )
}

export default navMenuItems