import React from 'react'

const menuItemText = ({ menuItemText, isActive, itemUrl, index }: { menuItemText: string, isActive: boolean, itemUrl: string, index: number }) => {
  return (
    <li key={index} className={`p-1 text-sm ${isActive ? 'text-slate-800' : 'text-slate-600'}`}>
      <a href={itemUrl} className="flex items-center">
        {menuItemText}
      </a>
    </li>
  )
}

export default menuItemText